@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@300&display=swap);
body {
  margin: 0 auto;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  margin: 0 3rem;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.container-home,
.container-success {
  background-image: url(/static/media/mvp-banner.e1143cc0.png);
  background-size: cover;
  background-position: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 100vh;
  color: white;
  font-family: "Barlow", sans-serif;
  font-size: 50px;
}
.teknolojik-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ce2829;
  padding: 4rem;
}
main,
header {
  font-family: "Barlow", sans-serif;
}
#order-pizza {
  border: none;
  border-radius: 2rem;
  background-color: #fdc913;
  padding: 15px 35px;
}

.home-slogan h1,
.container-slogan h1 {
  text-align: center;
  margin: 0.5rem 0;
}
.hero,
.confirm-order {
  margin: 0.5rem 0 0 0;
}
a {
  text-decoration: none;
  color: black;
  font-weight: bold;
}

.navbar nav {
  display: flex;
  color: white;
  font-family: "Barlow", sans-serif;
  font-size: 15px;
}

.container-orderpizza {
  display: flex;
  flex-direction: column;
}
.order-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ce2829;
}
.order-body {
  display: flex;
  flex-direction: column;
  line-height: 25px;
}

.pizza-name {
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-self: baseline;
  margin: 1.5rem 0;
  font-family: "Barlow", sans-serif;
  font-weight: bold;
}
.price-rate-comment {
  display: flex;
  justify-content: space-between;
  align-items: self-end;
  margin: 0 0 1.5rem 0;
  font-family: "Barlow", sans-serif;
  font-weight: bold;
}

.price-rate-comment h2 {
  font-size: 25px;
  font-family: "Barlow", sans-serif;
  font-weight: bold;
}

.price-rate-comment,
.story p {
  font-size: 15px;
  margin: 0 0 1.5rem 0;
  font-family: "Barlow", sans-serif;
  font-weight: lighter;
}
.size-picker-header,
.thickness-picker-header,
.extras-header,
.note-header,
.bill-header,
.customer-info-header {
  font-size: 20px;
  font-family: "Barlow", sans-serif;
  font-weight: bold;
  margin: 0 0 0.5rem 0;
}

.bill-header,
.extra-total-top,
.total-bill-top {
  margin: 1rem 2rem 0 2rem;
}
.size-picker,
.thickness-picker,
.extras p {
  font-family: "Barlow", sans-serif;
}
.size-and-thickness {
  display: flex;
  flex-direction: row;
  margin: 0 0 2rem 0;
}
.extra-picker {
  display: flex;
  flex-flow: wrap;
  font-family: "Barlow", sans-serif;
  font-weight: bolder;
}
.thickness {
  margin-left: 15rem;
}

.extras,
.note,
.customer-info {
  margin: 0 0 2rem 0;
}
.note-input,
.customer-info-input {
  width: 70vh;
  height: 40px;
  margin: 0 1rem 0 0;
}

.quantity {
  height: 50px;
}

.quantity button {
  background-color: #fdc913;
  border-radius: 0.2rem;
  border-color: #fdc913;
  width: 50px;
  height: 50px;
}

.quantity h3 {
  margin: 0 0.3rem;
  font-weight: bold;
  align-self: center;
  margin: 1rem;
}

.bill {
  display: flex;
  flex-direction: column;
}
.invalid-feedback {
  color: red;
}
.quantity,
.bill {
  display: flex;
  border-color: #fdc913;
  border: 1px solid #fdc913;
  border-radius: 0.2rem;
}
.total-bill {
  color: #ce2829;
}

.quantity-and-bill,
.extra-total-top,
.total-bill-top {
  display: flex;
  justify-content: space-between;
}
.quantity-and-bill {
  padding: 2rem 0 0 0;
  border-top: 1px solid gray;
}

.confirm-order {
  display: flex;
  justify-content: center;
  width: 40vh;
  background-color: #fdc913;
  padding: 1rem 2rem;
  font-family: "Barlow", sans-serif;
  font-size: 1.5rem;
  border: none;
  border-radius: 0.2rem;
  margin: 1rem 0 0 0;
}

.confirm-order-bttn {
  display: flex;
}

.confirm-order-bttn[disabled] {
  opacity: 0.5;
}
/* 

#### Renk Kodları

Sarı #FDC913, Açık Gri: #5F5F5F, Koyu Gri #292929, Kırmızı #CE2829, Bej: #FAF7F2 */

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

